&-stars {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 166px;
    height: 16px;
    margin-top: 16px;

    &-list {
      display: flex;

      &-star {
        width: 16px;
        height: 16px;
        margin-right: 4px;
        background: url(../images/star.svg) no-repeat center;

        &:last-child {
          margin-right: 0;
        }

        &:nth-child(-n + 4) {
          background: url(../images/star-active.svg) no-repeat center;
        }
      }
    }

    &-reviews {
      &-text {
        font-size: 10px;
        font-weight: 400;
        line-height: 14px;
        margin: 0;
      }
    }
  }
