@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header__logo-image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__text {
  margin-left: 20px;
}

.nav__text:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link.is-active, .nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  box-sizing: border-box;
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  margin: 0 auto;
  padding: 50px 0;
  display: grid;
}

@media (width <= 487px) {
  .catalog {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.catalog__card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  display: flex;
}

.catalog__card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.catalog__card-image {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: 19px;
  margin-right: 19px;
}

.catalog__card-info {
  margin: 40px 16px 0;
}

.catalog__card-info-text {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card-text {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.catalog__card-stars {
  justify-content: space-between;
  align-items: center;
  max-width: 166px;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.catalog__card-stars-list {
  display: flex;
}

.catalog__card-stars-list-star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.catalog__card-stars-list-star:last-child {
  margin-right: 0;
}

.catalog__card-stars-list-star:nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.catalog__card-stars-reviews-text {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.catalog__card-price {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 24px;
  display: flex;
}

.catalog__card-price-word {
  color: #616070;
}

.catalog__card-price-word-text {
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.catalog__card-price-text {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.catalog__card-button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 0;
  display: flex;
}

.catalog__card-button-text {
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.catalog__card-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

